@import "./sass-variables.sass";

.headerComponent {
    position: relative;
    height: 504px;
    width: 100vw;
    background-image: URL('https://cdn.pixabay.com/photo/2018/12/21/20/17/music-3888684_960_720.png');
    background-size: 75%;
    background-position: 180% 30%;
    // display: flex
    box-shadow: $smain;
    #headerContainer {
        position: relative;
        // height: 504px
        width: 1100px;
        margin: auto;
        // border: 5px solid red
        z-index: 6;
        #homeHamburger {
            position: fixed;
            top: 20px;
            display: flex;
            justify-content: space-between;
            // padding: 0 20px
            margin: 0 0 0 20px;
            width: 1060px;
            z-index: 15;
            a {
                text-decoration: none; }
            .homeButton {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 50px;
                height: 50px;
                background-color: $cmain;
                z-index: 15;
                transition: 0.3s;
                .fa {
                    color: $cdark;
                    font-size: 40px; }

                &:hover {
                    background-color: $cdark;
                    i {
                        color: $cmain; } } }

            .hamburger {
                // position: fixed
                width: 50px;
                height: 50px;
                padding: 10px;
                cursor: pointer;
                display: flex;
                border: 0;
                margin: 0;
                z-index: 10;
                background-color: $cmain;
                top: 30px;
                // right: 0px
                outline: none;
                transition: transform 0.1s ease-in-out;
                &:hover {
 }                    // background-color: $chover

                .hamburgerBox {
                    position: relative;
                    width: 35px;
                    height: 35px;
                    display: inline-block;
                    padding-top: 0px;
                    padding-bottom: 0px;

                    &:hover {}

                    .hamburgerInner {
                        width: 100%;
                        height: 5px;
                        background-color: $cdark;
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        transition: background-color .1s .1s ease-in-out; }
                    .hamburgerInnerHover {
                        width: 100%;
                        height: 5px;
                        background-color: $clight;
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        transition: background-color .1s .1s ease-in-out; }

                    .hamburgerInner::before,
                    .hamburgerInner::after,
                    .hamburgerInnerHover::before,
                    .hamburgerInnerHover::after {
                        width: 100%;
                        height: 5px;
                        background-color: black;
                        position: absolute;
                        left: 0;
                        content: '';
                        transition: transform .1s .1s ease-in-out; }

                    .hamburgerInner::before,
                    .hamburgerInnerHover::before {
                        top: -10px; }

                    .hamburgerInner::after,
                    .hamburgerInnerHover::after {
                        top: 10px; } } } } } }

.hamburgerActive {
    transform: translateX(0) !important;
    background-color: transparent !important;

    .hamburgerInner {
        background-color: transparent !important; } }

.hamburgerActive .hamburgerInner:before {
    transform: translateY(10px) rotate(45deg); }

.hamburgerActive .hamburgerInner:after {
    transform: translateY(-10px) rotate(-45deg); }

.hamburgerDisabledHover {
    .hamburgerInner {
        background-color: white; }
    .hamburgerInner:before,
    .hamburgerInnerHover:before {
        transform: translateY(-5px); }
    .hamburgerInner:after,
    .hamburgerInnerHover:after {
        transform: translateY(5px); } }

// .hamburgerDisabledHover
//     .hamburgerInner
//         background-color: white
.logo {
    position: absolute;
    top: 300px;
    left: 20px;
    z-index: 3 !important;
    height: 200px;
    width: 300px {
        // margin: auto
 }        // tramsform: translateX(-50%)

    img {
        height: 60%; } }

.sidebar {
    background-color: $clight;
    position: fixed;
    min-height: 200vh;
    z-index: 5;
    width: 50vw;
    min-width: 500px;
    padding-top: 100px;
    transform: translateX(100vw);
    transition: transform 0.2s ease-in-out;
    top: 0px;

    nav {
        height: 400px;
        display: flex;
        color: $cdark;
        justify-content: space-around;

        a {
            text-decoration: none;
            color: $cdark;
            transition: 0.2s;

            &:hover {
                color: $cmain;
                // text-shadow: .5px .5px $cdark
                transform: translateX(20px); } }

        ul {
            list-style: none;
            display: flex;
            flex-direction: column;
            justify-content: space-between; }

        .navList {
            align-items: flex-start;
            padding: 10px;

            li {
                padding: 10px;
                font-size: 25px; } }

        .socialList {
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            a {
                text-decoration: none;
                color: $cdark;
                font-size: 40px;
                text-align: center;
                padding: 10px;
                background-color: transparent;
                cursor: pointer;
                border: none;
                outline: none;
                transform: rotate(0deg); }
            li:first-child {
                a {
                    &:hover {
                        color: #FF0000; } } }
            li:nth-child(2) {
                a {
                    &:hover {
                        color: #4267B2; } } }
            li:nth-child(3) {
                a {
                    &:hover {
                        color: #833AB4; } } }
            li:nth-child(4) {
                a {
                    &:hover {
                        color: #1DA1F2; } } } } } }

.sidebarActive {
    transform: translateX(50vw) !important; }

